#footer-links{
  display: flex;
  padding: 3rem 0;
  background: $lighter-grey;
  justify-content: center;

  ul {

    .hed {
      font-family: 'Montserrat';
      letter-spacing: .01rem;
      font-size: 1.6rem;
      font-weight: 600;
      color: $grey;
      margin-bottom: 1rem;
    }

    li a {
      color: $grey;
      font-size: 1.6rem;
      line-height: 3.2rem;
      text-decoration: underline;
    }
  }

}


#footer-info {
  display: flex;
  background: black;
  justify-content: center;
  padding: 3rem 0;

  .image-logo {
    width: 30rem;
  }

  .hed, p, a {
    color: $white;
  }

  .hed {
    font-family: 'Montserrat';
    font-weight: 600;
    margin-bottom: 1rem;
  }

  ul li, p {
    font-size: 1.6rem;
  }

  a {
    font-size: 1.2rem;
    padding-right: 1rem;

    &:hover {
      color: $red;
    }
  }

  ul {
    margin: 0;
    padding: 0;

    li a {
      font-size: 1.6rem;
      border-bottom: 1px solid $white;
      line-height: 3.4rem;

      &:hover {
        color: $red;
        border-bottom-color: $red;
      }
    }
  }

  .footer-column {
    max-width: 40rem;
  }

  .footer-column:first-child {
    p {
      margin: 3rem 0;
    }
  }
}

.footer-container {
  min-height: 20rem;
  margin: 3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 140rem;
  width: 100%;

  ul {
    padding: 0;
    margin:3rem 0;
  }
}

@media (max-width: 800px) {
  .footer-container {
    justify-content: center;
    flex-direction: column;

    ul {
      margin: 3rem 0;
    }
  }

  #footer-info .footer-column {
    margin-bottom: 3rem;
  }
}
