@import "variables";
@import "navbar";
@import "typography";
@import "header";
@import "footer";
@import "override";

body {
  background: $white;
}

.container {
  padding-top: 0;
}

.hed--large.light-green::after {
  background: $light-green;
}

.image.light-green {
  border-color: $light-green;
}

.hed--large.blue::after {
  background: $blue;
}

.image.blue {
  border-color: $blue;
}

.hed--large.light-blue::after {
  background: $light-blue;
}

.image.light-blue {
  border-color: $light-blue;
}

.footer-line {
  text-align: center;
  font-size: 1.2rem;
  margin:12rem 0 3rem;
}

.pattern.areas-of-focus {
  max-width: 80rem;
  margin: 0 auto;
}
