.navbar {
  transform: translateX(0);
  position: relative;
  bottom: 0.4rem;

  ol {
    padding: 0;
    display: flex;
    list-style: none;
    align-items: center;
    margin: 0;

    li a {
      font-family: 'Montserrat';
      font-weight: 500;
      font-size: 1.6rem;
      color: $grey;
      margin: 1rem;
    }

    li.search {
      position: relative;
      top: 0.3rem;
      right: 0.9rem;
    }
  }
}

.ham {
  position: fixed;
  z-index: 10;
  top: 5rem;
  right: 3rem;
  width: 3rem;
  height: 3rem;
  border: 3px solid $grey;
  border-radius: 0.3rem;
  background: none;
  color: $white;
  font-weight: bold;
  font-size: 3rem;
  cursor: pointer;
  background-size: 100%;
  justify-content: center;
  align-items: center;
  display: none;
  flex-direction: column;

  div {
    width: 30%;
    height: .3rem;
    background: $grey;
    margin: 0.1rem;
    position: relative;
  }

  .top {
    transform: rotateZ(-45deg);
    transition: transform 0.3s ease;
  }
  .btm {
    transform: rotateZ(45deg);
    transition: transform 0.3s ease;
  }

  &.showClose {
    .top {
      transform: rotateZ(45deg);
    }
    .btm {
      transform: rotateZ(-45deg);
    }
  }
}

.fixed-position {
   // position: fixed;
}

@media (max-width: 900px) {
  .ham {
    display: flex;
  }

  .navbar{
    top: 0;
    right: 0;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    display: flex;
    overflow: hidden;
    position: fixed;
    padding: 3rem;
    transform: translateX(100%);
    box-sizing: border-box;
    background: rgba(255,255,255,0.9);
    justify-content: center;
    transition: transform 0.3s ease;
    align-items: center;

    ol {
      flex-direction: column;
      align-items: center;

      li a {
        padding: 1rem;
        display: flex;
        font-size: 2.4rem;
      }
    }
  }

  .showNav {
    transform: translateX(0);
  }
}
