a {
  color: $light-blue;

  &:hover {
    color: $blue;
  }
}

a.button {
  color: $white;
  background: $red;
}

label {
  font-size: 1.2rem;
}

ul {
  font-size: 1.6rem;
  line-height: 2.6rem;
  max-width: 46rem;
  list-style: disc;
  padding-left: 2rem;

  li {
    margin-bottom: 1rem;
  }
}

.hed {
  font-family: 'Merriweather', serif;
}

.hed.hed--large,  {
  color: black;
}
