#header {
  display: flex;
  justify-content: center;
  background: $lighter-grey;
  min-height: 15.2rem;
  width: 100%;
}

#header-container {
  display: flex;
  width: 100%;
  max-width: 144rem;
  align-items: center;
  padding: 5rem 3rem 6rem 6rem;
  justify-content: space-between;
}

#logo {
  height: 3.2rem;
  img{
    width: 22.1rem;
  }
}

@media (max-width: 1200px) {
  #header-container {
  padding: 5rem 3rem 6rem 3rem;
  }
}
